@mixin mobile {
    @media screen and (max-width:480px) {
        @content;
    }
}

@mixin tablet {
    @media screen and (max-width:768px) {
        @content;
    }
}

@mixin laptop {
    @media screen and (max-width:1200px) {
        @content;
    }
}


* {
    padding: 0;
    margin: 0;
    font-family: open sans, Arial, Helvetica Neue, sans-serif;
}

.app {
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    background: linear-gradient(17deg, #e7df24, #e78924, #e75524);
    background-size: 600% 600%;

    -webkit-animation: AnimationName 5s ease infinite;
    -moz-animation: AnimationName 5s ease infinite;
    animation: AnimationName 5s ease infinite;


    @-webkit-keyframes AnimationName {
        0% {
            background-position: 10% 0%
        }

        50% {
            background-position: 91% 100%
        }

        100% {
            background-position: 10% 0%
        }
    }

    @-moz-keyframes AnimationName {
        0% {
            background-position: 10% 0%
        }

        50% {
            background-position: 91% 100%
        }

        100% {
            background-position: 10% 0%
        }
    }

    @keyframes AnimationName {
        0% {
            background-position: 10% 0%
        }

        50% {
            background-position: 91% 100%
        }

        100% {
            background-position: 10% 0%
        }
    }

    @keyframes gradationTest {
        0% {
            background-color: #6CB8FF;
        }

        50% {
            background-color: #FFF66C;
        }

        100% {
            background-color: #FFA36C;
        }
    }

    .appWrapper {
        height: 90%;
        width: 70%;
        display: flex;
        gap: 20px;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        @include tablet {
            width: 100%;
            height: 100%;
        }

        .title {
            display: flex;
            justify-content: center;
            align-items: center;
            color: #dfdfdf;
            gap: 15px;
            height: 30px;

            @include tablet {
                margin-top: 10px;
            }

            h1 {
                font-size: 30px;
                color: #dfdfdf;
                font-weight: 500;
            }
        }

        .input {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 45%;
            height: 30px;

            @include tablet {
                width: 70%;
            }

            form {
                display: flex;
                align-items: center;
                justify-content: center;
                width: 100%;

                input {
                    height: 30px;
                    width: calc(100% - 30px);
                    padding: 0px 10px;
                    border: none;
                    outline: none;
                    font-size: 13px;
                    background-color: transparent;
                    color: #dfdfdf;

                    &::placeholder {
                        color: rgba(236, 228, 228, 0.574);
                    }

                    &:focus {
                        -webkit-transition: 0.3s;
                        -moz-transition: 0.3s;
                        -ms-transition: 0.3s;
                        -o-transition: 0.3s;
                        transition: 0.3s;
                        border-bottom: 1px solid gray;

                    }
                }

                button {
                    border: none;
                    height: 30px;
                    width: 30px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    background-color: transparent;
                    cursor: pointer;
                    color: #dfdfdf;

                    &:hover {
                        color: #979191;
                    }
                }
            }
        }

        .todoList {
            width: 45%;
            overflow-y: scroll;
            height: calc(100% - 60px);

            @include tablet {
                width: 70%;
            }

            &::-webkit-scrollbar {
                width: 3px;
            }

            &::-webkit-scrollbar-thumb {
                background-color: #dfdfdf;
                border-radius: 10px;
            }

            .todo {
                display: flex;
                justify-content: center;
                align-items: center;
                margin-bottom: 10px;
                border-bottom: 1px #dfdfdf dotted;



                &.checked {
                    background-color: rgba(84, 87, 49, 0.755);

                    .text {
                        text-decoration: line-through;

                        p {
                            color: black;
                        }
                    }

                    .icons {
                        label {
                            color: black;

                            &:hover {
                                color: #13038e;
                            }
                        }

                        button {
                            color: black;

                            &:hover {
                                color: rgb(105, 5, 5);
                            }
                        }
                    }

                }




                .text {
                    width: 100%;
                    height: 30px;
                    display: flex;
                    align-items: center;
                    padding: 0px 10px;


                    p {
                        font-size: 12px;
                        color: #dfdfdf;
                    }
                }

                .icons {
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    label {
                        height: 30px;
                        width: 30px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        cursor: pointer;
                        color: #13038e;
                        cursor: pointer;

                        &:hover {
                            color: #948af3;
                        }
                    }

                    button {
                        height: 30px;
                        width: 30px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        cursor: pointer;
                        background-color: transparent;
                        border: none;
                        color: rgb(105, 5, 5);

                        &:hover {
                            color: rgb(208, 4, 4);
                        }
                    }
                }
            }
        }
    }
}